<svelte:options tag="fds-image-editor-gradient-dialog"></svelte:options>
<script>
    import {get_current_component} from "svelte/internal"


    import {toollayer as toolback} from "./stores";
    let toollayer
    if(globalThis.gyre && globalThis.gyre.paletteValues.tools){
        toollayer = globalThis.gyre.paletteValues.tools.fdsimageeditorgradient;
    }  else {
        toollayer =toolback;
    }


    let host = get_current_component().$$.root.host
    let value = "";

    export let toolinst;

    function change(name, value) {
        host.dispatchEvent(new CustomEvent("change", {detail: {name: name, value: value}}))
    }

    function changeList(value) {
        //document.getElementById("gradientTool").value=value
        $toollayer.value = value.value;
    }

    function resetLine() {
        $toollayer.resetLine = true;
    }

    function hexToRgb(hex) {
        // Remove the '#' if it is present
        hex = hex.replace(/^#/, '');

        // If the hex is shorthand like '#fff', expand it to '#ffffff'
        if (hex.length === 3) {
            hex = hex.split('').map(char => char + char).join('');
        }

        // Parse the hex string into RGB values
        const r = parseInt(hex.substring(0, 2), 16);
        const g = parseInt(hex.substring(2, 4), 16);
        const b = parseInt(hex.substring(4, 6), 16);

        return `${r}, ${g}, ${b}`;
    }

    $: if ($toollayer) {
        refresh();
    }

    if (globalThis.gyre && !value) {
        let start = hexToRgb(globalThis.gyre.paletteValues.foregroundColor);
        let stop = hexToRgb(globalThis.gyre.paletteValues.backgroundColor);
        value = `10:${start}
            90:${stop}`;
        $toollayer.value = value;
    }


    export function refresh() {
        value = $toollayer.value;
    }


    export let editcoord


</script>

<div class="show" style="display: inline-flex;">

    <div style="display:flex;align-items: center">
        <!-- svelte-ignore a11y-click-events-have-key-events -->
        <fds-image-editor-button type="button"
                                 on:click={() => { editcoord=!editcoord; $toollayer.editcoord =  editcoord ; change('editcoord',editcoord); }}>
            Parameters
        </fds-image-editor-button>
        <fds-image-editor-button type="button" on:click={() => { $toollayer.done = true; }}>Done
        </fds-image-editor-button>
        <fds-image-editor-button type="button" on:click={() => { $toollayer.cancel = true; }}>Cancel
        </fds-image-editor-button>

    </div>


    {#if editcoord  }
        <div class="editcoord">
            <div class="formLine">
                <fds-gradient-editor value="{value}" on:change={(ev) => { changeList(ev.detail) }}
                                     id="gradientList"></fds-gradient-editor>
            </div>
            <div class="formLine">
                <div style="display:flex;align-items: center">
                    <fds-image-editor-button type="button" on:click={() => { resetLine() }}>Reset Line
                    </fds-image-editor-button>

                </div>
            </div>
        </div>
    {/if}
</div>

