<svelte:options tag="fds-image-editor-gradient"></svelte:options>
<script>
  import { onMount, onDestroy,createEventDispatcher } from 'svelte';
  import {get_current_component} from 'svelte/internal'


  import {toollayer as toolback} from "./stores";
  let toollayer
  if(globalThis.gyre && globalThis.gyre.paletteValues.tools){
    toollayer = globalThis.gyre.paletteValues.tools.fdsimageeditorgradient;
  }  else {
    toollayer =toolback;
  }

  export let opacity;
  if (globalThis.gyre && globalThis.gyre.paletteValues.selectedLayer) opacity = globalThis.gyre.paletteValues.selectedLayer.opacity;
  import Dialog from "./Dialog.svelte";
  //let host = get_current_component()
  let host = get_current_component().$$.root.host
  let svgEl; // Reference to the SVG element
  let line = {      x1: 0, y1: 0,      x2: 10,      y2: 10    }
  export let value
  let layer={width:512,height:512}
  let zoom;
  if(globalThis.gyre){
    zoom = globalThis.gyre.canvas.zoom || 100;
  }
  let lastZoom=zoom;
  export  let ratio = window.devicePixelRatio;
  let p = parseFloat(zoom) / 100 / ratio;
  let noscaleline = {};


  let draggingPoint = null
  let draggingIndex = null
  let lasteditcoord;
  if (globalThis.gyre){
    setLayer()
  }






  // List of points along the line, each with a percentage and a color
  let points = [ ];
  let start_fill_color = "white"
  let end_fill_color = "white"

  let oldline;
  let oldpoints;
  let oldwidth
  let oldheight

 function setLayer(){
   layer={width:globalThis.gyre.canvas.width*p,height:globalThis.gyre.canvas.height*p}
 }

  export function resetLine() {
    line.x1=layer.width*0.1
    line.y1=layer.height*0.1
    line.x2=layer.width*0.9
    line.y2=layer.height*0.9
    setValues();
  }

  function createNoScaleLine(){
    for (const key in line) {
      if (line.hasOwnProperty(key)) {
        noscaleline[key] = line[key] / p;
      }
    }
  }

  function reCalc(num) {
    let newCoord = num / p;
    return newCoord
  }


  function value_to_internal(input) {
    points=[]
    let lines = input.trim().split('\n');
    // Parse each line
    lines.forEach(line => {
      let [percentageStr, colorStr] = line.split(':');
      let percentage = parseFloat(percentageStr) / 100; // Convert to decimal
      let rgbValues = colorStr.split(',').map(Number);
      let hexColor = '#' + rgbValues.map(c => c.toString(16).padStart(2, '0')).join('').toUpperCase();

      points.push({ percentage, color: hexColor });
    });

    // Sort points by percentage
    points.sort((a, b) => a.percentage - b.percentage);

    // Set start_fill_color
    let zeroEntry = points.find(point => point.percentage === 0);
    if (zeroEntry) {
      start_fill_color = zeroEntry.color;
    } else {
      start_fill_color = points[0].color;
    }

    // Set end_fill_color
    let hundredEntry = points.find(point => point.percentage === 1);
    if (hundredEntry) {
      end_fill_color = hundredEntry.color;
    } else {
      end_fill_color = points[points.length - 1].color;
    }

  }

  function internal_to_value() {
    // Include start_fill_color at percentage 0.0 if not present
    // let hasStartColor = points.some(point => point.percentage === 0.0);
    //  if (!hasStartColor && start_fill_color) {
    //  points.push({ percentage: 0.0, color: start_fill_color });
    //  }

    // Include end_fill_color at percentage 1.0 if not present
    //    let hasEndColor = points.some(point => point.percentage === 1.0);
    //  if (!hasEndColor && end_fill_color) {
    //   points.push({ percentage: 1.0, color: end_fill_color });
    //  }

    // Sort points by percentage
    points.sort((a, b) => a.percentage - b.percentage);

    // Function to convert hex color to RGB values
    function hexToRgb(hex) {
      // Remove '#' if present
      hex = hex.replace('#', '');

      // Handle shorthand hex colors (#F00)
      if (hex.length === 3) {
        hex = hex.split('').map(h => h + h).join('');
      }

      let bigint = parseInt(hex, 16);
      let r = (bigint >> 16) & 255;
      let g = (bigint >> 8) & 255;
      let b = bigint & 255;

      return `${r},${g},${b}`;
    }

    // Convert to the string format
    let outputLines = points.map(point => {
      let percentageInt =  point.percentage * 100;
      let color = point.color;

      // Check if the color is a hex code
      if (typeof color === 'string' && color.startsWith('#')) {
        color = hexToRgb(color);
      }

      return `${percentageInt}:${color}`;
    });

    // Join lines into a single string
    let outputString = outputLines.join('\n');
    return outputString
  }

  function recreateLineScaled(){
    if(noscaleline.x1 != void 0 && noscaleline.x2 != void 0 ){
      setTimeout(() => {
        let tmpline = {...noscaleline}
        Object.keys(tmpline).forEach(key => {
          tmpline[key] = tmpline[key] * p;
        });
        line = {...tmpline}
        setValues();
      }, 100);

     }
  }

  $: if($toollayer){
    refresh();
  }

  export function refresh(){
    if(globalThis.gyre){ zoom = globalThis.gyre.canvas.zoom || 100; }
    p = parseFloat(zoom) / 100 / ratio;
    setLayer();
    if (globalThis.gyre && globalThis.gyre.paletteValues.selectedLayer) opacity = globalThis.gyre.paletteValues.selectedLayer.opacity;
    if($toollayer.value) value = $toollayer.value;
    if($toollayer.resetLine){
      console.log('reset line');
      resetLine();
      $toollayer.resetLine = false;
    }
    if($toollayer.done){
      console.log('done');
      $toollayer.done = false;
      convertCanvasToImage();
    }
    if($toollayer.cancel){
      console.log('cancel');
      host.dispatchEvent(new CustomEvent('change', {detail:{'type':'cancelimagegenerate'}}))
      $toollayer.cancel = false;
    }
    if($toollayer.editcoord!==lasteditcoord){
      host.dispatchEvent(new CustomEvent('change', {detail:{'type':'editcoord','value':$toollayer.editcoord}}))
      lasteditcoord = $toollayer.editcoord;
    }


  }


  $: if (value) {
    value_to_internal(value)
    calculatePointPositions();
    refresh();
  }


  $: if (layer && !line.x1) {
    resetLine()
  }

  $: if(lastZoom && lastZoom!=zoom){
    recreateLineScaled();
    lastZoom = zoom;
  }


  function startDrag(point, index = null) {
    draggingPoint = point;
    draggingIndex = index;
  }

  function stopDrag() {
    draggingPoint = null;
    draggingIndex = null;
  }

  function drag(event) {
    if (!draggingPoint) return;

    // Calculate coordinates relative to SVG element
    const svgBounds = svgEl.getBoundingClientRect();
    const offsetX = event.clientX - svgBounds.left;
    const offsetY = event.clientY - svgBounds.top;

    if (draggingPoint === 'x1') {
      line.x1 = offsetX;
      line.y1 = offsetY;
      calculatePointPositions();
    } else if (draggingPoint === 'x2') {
      line.x2 = offsetX;
      line.y2 = offsetY;
      calculatePointPositions();
    } else if (draggingPoint === 'middlePoint') {
      // Adjust only the specific point being dragged
      const dx = line.x2 - line.x1;
      const dy = line.y2 - line.y1;
      const length = Math.sqrt(dx * dx + dy * dy);
      const t = ((offsetX - line.x1) * dx + (offsetY - line.y1) * dy) / (length * length);
      points[draggingIndex].percentage = Math.max(0, Math.min(1, t)); // Keep percentage between 0 and 1
    }

    setValues();
  }



  function setValues(){
    createNoScaleLine();
    // Recalculate positions for each point
    calculatePointPositions();
    value=internal_to_value();
    $toollayer.value = value;
    host.dispatchEvent(new CustomEvent('change', {detail:{'type':'coordinates',value}}))
  }



  function calculatePointPositions() {
    points.forEach((point) => {
      point.x = line.x1 + point.percentage * (line.x2 - line.x1);
      point.y = line.y1 + point.percentage * (line.y2 - line.y1);
    });
    drawGradientLine()
    points = points; // Trigger reactivity
  }

  // Initial calculation of points' positions
  // calculatePointPositions();

  onMount(() => {
    window.addEventListener('mousemove', drag);
    window.addEventListener('mouseup', stopDrag);
    if (value) {
      calculatePointPositions()
    }
  });

  onDestroy(() => {
    window.removeEventListener('mousemove', drag);
    window.removeEventListener('mouseup', stopDrag);
  });

  //  let gradientDiv=null
  let canvas=null



  function drawGradientLine(ignoreclientSize) {
    if (!canvas) return;
    const ctx = canvas.getContext('2d');

    // Ensure the canvas is sized correctly
    if(ignoreclientSize){
      canvas.width =  canvas.width;
      canvas.height =  canvas.height;
    } else {
      canvas.width = canvas.clientWidth || canvas.width;
      canvas.height = canvas.clientHeight || canvas.height;
    }

    // Check for zero dimensions
    if (canvas.width === 0 || canvas.height === 0) {
      console.error('Canvas dimensions are zero. Please set canvas width and height.');
      return;
    }

    // Calculate the angle of the line
    const angleRad = Math.atan2(line.y2 - line.y1, line.x2 - line.x1);

    // Calculate the length of the gradient line (long enough to cover the canvas diagonally)
    const canvasDiagonal = Math.sqrt(canvas.width ** 2 + canvas.height ** 2);

    // Calculate gradient start and end points
    const gradientStartX = line.x1 - canvasDiagonal * Math.cos(angleRad);
    const gradientStartY = line.y1 - canvasDiagonal * Math.sin(angleRad);
    const gradientEndX = line.x2 + canvasDiagonal * Math.cos(angleRad);
    const gradientEndY = line.y2 + canvasDiagonal * Math.sin(angleRad);

    // Create gradient
    const gradient = ctx.createLinearGradient(gradientStartX, gradientStartY, gradientEndX, gradientEndY);

    // Add start color stop
    gradient.addColorStop(0, start_fill_color);

    // Calculate total length of the line for percentage calculations
//    const lineLength = Math.hypot(line.x2 - line.x1, line.y2 - line.y1);

    // Add intermediate color stops
    points.forEach(point => {
      // Position along the line
      const pointX = line.x1 + (line.x2 - line.x1) * point.percentage;
      const pointY = line.y1 + (line.y2 - line.y1) * point.percentage;

      // Calculate distance from gradient start point
      const distanceFromGradientStart = Math.hypot(pointX - gradientStartX, pointY - gradientStartY);
      const gradientLength = Math.hypot(gradientEndX - gradientStartX, gradientEndY - gradientStartY);

      // Calculate the gradient stop percentage
      const gradientPercentage = distanceFromGradientStart / gradientLength;

      gradient.addColorStop(gradientPercentage, point.color);
    });

    // Add end color stop
    gradient.addColorStop(1, end_fill_color);

    // Set fill style and fill the entire canvas
    ctx.fillStyle = gradient;
    ctx.fillRect(0, 0, canvas.width, canvas.height);
  }

  function undoAdd() {
    if (!globalThis.gyre) return;
    let urlAfter = canvas.toDataURL("image/png");
    let urlBefore =  globalThis.gyre.paletteValues.selectedLayer.url;

    globalThis.gyre.undo_redo_add('gradient', 'imagegenerated', { urlBefore, urlAfter }, (state, action) => {
      let backup
      if (action === 'undo') backup  = state.data.urlBefore
      if (action === 'redo') backup  = state.data.urlAfter;

      globalThis.gyre.paletteValues.selectedLayer.url = backup

      if(globalThis.gyre.paletteValues.selectedLayer && globalThis.gyre.paletteValues.selectedLayer._refreshCallback){
        globalThis.gyre.paletteValues.selectedLayer._refreshCallback();
      }

    })
  }


  function convertCanvasToImage() {
    recalculateNoZoomCoordinates();
    // Get the data URL of the canvas
    const dataURL = canvas.toDataURL("image/png");
    setBackCoordinates();
    undoAdd();
    host.dispatchEvent(new CustomEvent('change', {detail: {'type':'imagegenerated','dataURL':dataURL}}));

  }

  function  recalculateNoZoomCoordinates(){
    oldline =  {...line}
    line =  {      x1: reCalc(line.x1), y1: reCalc(line.y1), x2: reCalc(line.x2),y2: reCalc(line.y2)}
    oldpoints =  JSON.parse(JSON.stringify(points));
    points = points.map((el)=>{
      el.x = reCalc(el.x);
      el.y = reCalc(el.y);
      return el;
    });
    oldwidth =  canvas.width;
    oldheight =  canvas.height;
    canvas.width =  reCalc(canvas.width);
    canvas.height =  reCalc(canvas.height);
    drawGradientLine('ignoreclientSize');
  }


  function setBackCoordinates(){
    canvas.width = oldwidth;
    canvas.height = oldheight
    line = oldline;
    points = oldpoints;
    drawGradientLine();
  }



</script>
<div style="width:{layer.width}px;height:{layer.height}px;position:relative">
  <canvas style="position:absolute;width:{layer.width}px;height:{layer.height}px;z-index:-10;opacity:{(opacity/100)};" bind:this={canvas}></canvas>
  <svg bind:this={svgEl} width={layer.width} height={layer.height}>
    <!-- Draw the line -->
    <line
            x1={line.x1}
            y1={line.y1}
            x2={line.x2}
            y2={line.y2}
            stroke="black"
            stroke-width="2"
    />

    <!-- Start point -->
    <circle
            cx={line.x1}
            cy={line.y1}
            r="8"
            fill="white"
            stroke="black"
            stroke-width="2"
            on:mousedown={() => startDrag('x1')}
            on:mouseup={stopDrag}
    />

    <!-- End point -->



    <circle
            cx={line.x2}
            cy={line.y2}
            r="8"
            fill="white"
            stroke="black"
            stroke-width="2"
            on:mousedown={() => startDrag('x2')}
            on:mouseup={stopDrag}
    />




    <!-- Additional points along the line -->
    {#each points as { percentage, color, x, y }, index}


      <circle
              cx={x}
              cy={y}
              r="6"
              fill={color}
              stroke="white"
              stroke-width="2"
              on:mousedown={() => startDrag('middlePoint', index)}
              on:mouseup={stopDrag}
      />

      <circle
              cx={x}
              cy={y}
              r="8"
              fill="none"
              stroke="black"
              stroke-width="2"
              on:mousedown={() => startDrag('middlePoint', index)}
              on:mouseup={stopDrag}
      />



    {/each}
  </svg>
</div>

<style>
  circle {
    cursor: pointer;
  }
</style>
